* {
  margin: 0;
  padding: 0;
}
body {
  box-sizing: border-box;
  font-family: poppins;
  background-color: #b4f2e1;
}
.divStyle {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-height: 100vh;
  margin-top: -30px;
}
.centerStyle {
  padding: 20px;
  background-color: #ffe9c5;
  color: #fa9191;
  border-radius: 20px;
}
